import React from "react";
import "./App.css";
import Dashboard from "./components/Dashboard";
import { HashRouter, Navigate, Route, Routes } from "react-router-dom";
import PropertyView from "./components/property/PropertyView";
import PropertyEditForm from "./components/property/PropertyEditForm";
import { Login } from "./components/auth/Login";
import { AuthContextProvider } from "./security/AuthContext";
import { OAuth2Redirect } from "./components/auth/OAuth2Redirect";
import { Main } from "./components/Main";
import { AdminPage } from "./components/admin/AdminPage";
import AdminRoute from "./components/admin/AdminRoute";
import { ProjectView } from "./components/project/ProjectView";
import { createTheme, ThemeProvider } from "@mui/material";

const defaultTheme = createTheme({
  palette: {
    primary: { main: "#628395" },
  },
});
function App() {
  return (
    <ThemeProvider theme={defaultTheme}>
      <AuthContextProvider>
        <HashRouter>
          <Routes>
            <Route path="/" element={<Main />}>
              <Route index element={<Navigate to="/dashboard" replace />} />
              <Route path="dashboard" element={<Dashboard />} />
              <Route path="properties">
                <Route index element={<Navigate to={"/dashboard"} replace />} />
                <Route path=":propertyId">
                  <Route index element={<PropertyView />} />
                  <Route
                    path={"projects/:projectId"}
                    element={<ProjectView />}
                  />
                  <Route
                    path="edit"
                    element={<PropertyEditForm mode={"edit"} />}
                  />
                </Route>
                <Route
                  path="create"
                  element={<PropertyEditForm mode={"create"} />}
                />
              </Route>

              <Route
                path={"admin"}
                element={
                  <AdminRoute>
                    <AdminPage />
                  </AdminRoute>
                }
              />
            </Route>

            <Route path={"/login"} element={<Login />} />
            <Route path={"/oauth2/redirect"} element={<OAuth2Redirect />} />
          </Routes>
        </HashRouter>
      </AuthContextProvider>
    </ThemeProvider>
  );
}

export default App;
