import React, { useState } from "react";
import {
  Accordion,
  AccordionDetails,
  AccordionSummary,
  Box,
  Button,
  ButtonGroup,
  Chip,
  Divider,
  IconButton,
  Tab,
  Tabs,
  Typography,
} from "@mui/material";
import { Link, useParams } from "react-router-dom";
import { usePropertyFetch } from "../../hooks/PropertyFetch";
import { useProjectsQuery } from "../../hooks/ProjectFetch";
import { Add, Construction } from "@mui/icons-material";
import { idFromPath } from "../../util/PathUtil";
import { BASE_API_URL } from "../../util/Constants";
import { TitleBar } from "../TitleBar";
import ExpandMoreIcon from "@mui/icons-material/ExpandMore";

function ProjectList() {
  const { response } = useProjectsQuery(`${BASE_API_URL}/projects`);
  return (
    <Box justifyContent={"center"}>
      <IconButton
        size={"large"}
        color={"primary"}
        sx={{ position: "absolute", bottom: 16, right: 16 }}
        component={Link}
        to={`projects/create`}
      >
        <Add />
      </IconButton>
      {response?._embedded?.projects?.map((project, index) => (
        <Accordion key={index}>
          <AccordionSummary
            expandIcon={<ExpandMoreIcon />}
            sx={{ verticalAlign: "middle" }}
          >
            <Box
              display={"flex"}
              flexDirection={"row"}
              sx={{ alignItems: "center" }}
            >
              <Construction />
              <Chip
                label={project.status}
                variant={"filled"}
                style={{ marginRight: "1rem", marginLeft: "1rem" }}
              />
              <Typography>{project.description}</Typography>
            </Box>
          </AccordionSummary>
          <AccordionDetails>
            <ButtonGroup variant={"contained"}>
              <Button
                component={Link}
                to={`projects/${idFromPath(project._links.self.href)}`}
              >
                View
              </Button>
              <Button
                component={Link}
                to={`projects/${idFromPath(project._links.self.href)}/edit`}
              >
                Edit
              </Button>
            </ButtonGroup>
          </AccordionDetails>
        </Accordion>
      ))}
    </Box>
  );
}

interface TabPanelProps {
  children?: React.ReactNode;
  index: number;
  value: number;
}

function CustomTabPanel(props: TabPanelProps) {
  const { children, value, index, ...other } = props;

  return (
    <div
      role="tabpanel"
      hidden={value !== index}
      id={`simple-tabpanel-${index}`}
      aria-labelledby={`simple-tab-${index}`}
      {...other}
    >
      {value === index && <Box sx={{ p: 3 }}>{children}</Box>}
    </div>
  );
}

export default function PropertyView() {
  const { propertyId } = useParams();
  const { property } = usePropertyFetch(
    `${BASE_API_URL}/properties`,
    propertyId,
  );
  const [tabValue, setTabValue] = useState(0);
  const handleTabChange = (_event: React.SyntheticEvent, newValue: number) => {
    setTabValue(newValue);
  };
  const address = property?.address;
  return (
    <>
      <TitleBar title={"Property"} />
      <Box>
        <Typography align={"center"} sx={{ fontWeight: "bold" }}>
          {" "}
          {property?.name}
        </Typography>
        {address && (
          <Typography align={"center"}>
            {address?.address} {address?.city}, {address?.state} {address?.zip}
          </Typography>
        )}
        <Divider />

        <Tabs value={tabValue} onChange={handleTabChange}>
          <Tab label={"Recent Projects"} />
        </Tabs>
        <CustomTabPanel index={0} value={tabValue}>
          <ProjectList />
        </CustomTabPanel>
      </Box>
    </>
  );
}
