import React, { ReactElement, useContext, useEffect } from "react";
import {
  CircularProgress,
  Container,
  CssBaseline,
  Grid,
  IconButton,
  Stack,
  Typography,
} from "@mui/material";
import { PropertyCard } from "./property/PropertyCard";
import { Property, usePropertiesQuery } from "../hooks/PropertyFetch";
import { BASE_API_URL } from "../util/Constants";
import { TitleBar } from "./TitleBar";
import { AuthContext } from "../security/AuthContext";
import { Link, useNavigate } from "react-router-dom";
import { Add } from "@mui/icons-material";

function PropertiesGrid(props: {
  loading: boolean;
  error: any;
  propertiesArray?: Property[];
}) {
  const { loading, error, propertiesArray } = props;

  const placeholder =
    !loading && !error ? <Typography>No properties found!</Typography> : null;

  const showContent = !loading && !error;

  return (
    <Grid
      container
      rowSpacing={{ xs: 1, sm: 2, md: 3 }}
      justifyContent={"center"}
      columnSpacing={{ xs: 1, sm: 2, md: 3 }}
      columns={{ xs: 2, sm: 4, md: 8, lg: 12 }}
      sx={{ m: 4, p: 4 }}
    >
      {loading && <CircularProgress />}
      {showContent && propertiesArray?.length
        ? propertiesArray.map((property, index) => (
            <PropertyCard key={index} property={property} />
          ))
        : placeholder}
      {showContent && (
        <IconButton
          size={"large"}
          color={"primary"}
          sx={{ position: "absolute", bottom: 16, right: 16 }}
          component={Link}
          to={"/properties/create"}
        >
          <Add />
        </IconButton>
      )}
    </Grid>
  );
}

export default function Dashboard(): ReactElement {
  const {
    response: propertyResponse,
    isLoading,
    error,
  } = usePropertiesQuery(`${BASE_API_URL}/properties`);
  const properties = propertyResponse?._embedded?.properties;

  const { loggedIn } = useContext(AuthContext);
  const navigate = useNavigate();

  useEffect(() => {
    if (loggedIn === false) {
      navigate("/login");
    }
  }, [loggedIn, navigate]);

  return (
    <>
      <CssBaseline />
      <TitleBar title={"Properties"} />
      <Container maxWidth="sm">
        <Typography
          variant="h5"
          align="center"
          color="text.secondary"
          paragraph
        >
          Available Properties
        </Typography>
        <Stack
          sx={{ pt: 4 }}
          direction="row"
          spacing={2}
          justifyContent="center"
        ></Stack>
      </Container>
      <PropertiesGrid
        loading={isLoading}
        error={error}
        propertiesArray={properties}
      />
    </>
  );
}
