import {
  Box,
  Button,
  Card,
  CardActions,
  CardContent,
  CardMedia,
  Grid,
  Typography,
} from "@mui/material";
import React from "react";
import { Property } from "../../hooks/PropertyFetch";
import { Link } from "react-router-dom";
import { idFromPath } from "../../util/PathUtil";
import { DEFAULT_IMAGE_URL } from "../../util/Constants";

export function PropertyCard(props: { property: Property }) {
  return (
    <Grid item xs={2}>
      <Card
        sx={{
          height: "100%",
          display: "flex",
          flexDirection: "column",
          alignItems: "center",
          maxWidth: "300px",
        }}
        raised
      >
        <CardContent
          style={{ paddingBottom: "8px" }}
          sx={{
            height: "100%",
            display: "flex",
            flexDirection: "column",
            width: "100%",
            paddingBottom: "8px",
          }}
        >
          <Box display={"flex"} flexDirection={"row"} justifyContent={"center"}>
            <CardMedia
              component="div"
              sx={{
                // 16:9
                pt: "56.25%",
                maxWidth: "200px",
                width: "100%",
              }}
              image={DEFAULT_IMAGE_URL}
            />
          </Box>
          <Typography gutterBottom variant="h5" component="h2" align={"center"}>
            {props.property.name}
          </Typography>
          {props.property.address ? (
            <div>
              <Typography align={"center"}>
                {props.property.address?.address}
              </Typography>
              <Typography align={"center"}>
                {`${props.property.address?.city}, ${props.property.address?.state} ${props.property.address?.zip}`}
              </Typography>
            </div>
          ) : (
            <Typography align={"center"}>No address</Typography>
          )}
          <CardActions
            disableSpacing
            sx={{
              mt: "auto",
              paddingTop: "16px",
              justifyContent: "space-evenly",
            }}
          >
            {props.property._links && (
              <Button
                size="small"
                component={Link}
                to={`/properties/${idFromPath(
                  props.property._links?.self.href,
                )}`}
                variant={"contained"}
              >
                View
              </Button>
            )}
            {props.property._links && (
              <Button
                size="small"
                component={Link}
                to={`/properties/${idFromPath(
                  props.property._links?.self.href,
                )}/edit`}
                variant={"contained"}
              >
                Edit
              </Button>
            )}
          </CardActions>
        </CardContent>
      </Card>
    </Grid>
  );
}
