import { useContext, useEffect, useState } from "react";
import { Links, Page } from "../model/FetchResponse";
import { BASE_API_URL } from "../util/Constants";
import { addAuthHeaders } from "../util/FetchUtils";
import { AuthContext } from "../security/AuthContext";

export interface Note {
  summary: string;
  description: string;
}

export interface Estimate {
  status: string;
  amount: number;
  receivedDate: any;
  updatedDate: any;
}

export interface Project {
  status: string;
  description: string;
  notes?: Note[];
  estimates?: Estimate[];
  _links: Links;
}

export interface ProjectWrapper {
  projects: Project[];
}

interface ProjectListResponse {
  _embedded: ProjectWrapper;
  _links: {
    self: {
      href: string;
    };
    profile: {
      href: string;
    };
    search: {
      href: string;
    };
  };
  page: Page;
}

export interface ProjectQueryResponse {
  isLoading: boolean;
  response?: ProjectListResponse;
  error?: any;
}

export interface ProjectFetchResponse {
  isLoading: boolean;
  project?: Project;
  error?: any;
}

export function useProjectsQuery(url: string): ProjectQueryResponse {
  const { login } = useContext(AuthContext);
  const [isLoading, setIsLoading] = useState(false);
  const [response, setResponse] = useState<ProjectListResponse | undefined>(
    undefined,
  );
  const [error, setError] = useState(undefined);

  useEffect(() => {
    setIsLoading(true);

    const fetchData = async () => {
      try {
        const response = await fetch(url, {
          headers: {
            ...addAuthHeaders(login),
          },
        });
        const responseBody = (await response.json()) as ProjectListResponse;
        setResponse(responseBody);
      } catch (e: any) {
        console.error(e);
        setError(e);
      } finally {
        setIsLoading(false);
      }
    };
    fetchData().then(() => console.debug(`Project list received`));
  }, [url, login]);

  return { isLoading, response, error };
}

export function useProjectFetch(
  _propertyId?: string,
  projectId?: string,
): ProjectFetchResponse {
  const { login } = useContext(AuthContext);
  const [isLoading, setIsLoading] = useState(false);
  const [project, setProject] = useState<Project | undefined>(undefined);
  const [error, setError] = useState(undefined);

  const url = `${BASE_API_URL}/projects/${projectId}`;

  useEffect(() => {
    setIsLoading(true);

    const fetchData = async () => {
      try {
        const response = await fetch(url, {
          headers: {
            ...addAuthHeaders(login),
          },
        });
        const responseBody = (await response.json()) as Project;
        setProject(responseBody);
      } catch (e: any) {
        console.error(e);
        setError(e);
      } finally {
        setIsLoading(false);
      }
    };
    fetchData().then(() => console.debug(`Project response received`));
  }, [setIsLoading, login, url]);

  return { isLoading, project, error };
}
